@import 'globals.scss';
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,700;1,900&display=swap');


*{
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Poppins', Helvetica, sans-serif;
  background-color: $color-body-background;
}


a {
  color: inherit;
  text-decoration: none;
}

h1{
  font-size: 48px;
  font-weight: 900;
  margin: 10px 0px;
  @include media-query($tablet){
    font-size: 42px;
  }
  @include media-query($mobile){
    font-size: 24px;
  }
}

h2{
  font-size: 32px;
  font-weight: 700;
  margin: 10px 0px;
  @include media-query($tablet){
    font-size: 26px;
  }
  @include media-query($mobile){
    font-size: 22px;
  }
}

h3{
  font-size: 18px;
}

h4{
  font-size: 1.1em;
  font-weight: 400;
  margin: 10px 0px;

}




.infobar {
  @include flex-space(space-between);
  @include padding-lateral;
  @include hide-on($tablet);
  @include bg-schema($dark, $light);
  @include size(100%, 40px);
  font-size: 0.9em;

  .info-container{
    @include flex-space(space-between);
    @include horizontal-spacing($spacing5);
  }
}


.footer{  
  @include flex-space(space-between);
  @include size(100%, 260px);
  @include bg-schema($dark, $light);
  flex-direction: column;  
  padding-top: $spacing4;  
  
  .info-container{
    @include flex-space(space-between);
    flex-direction: column;
    .info-social-media{
      margin-top: $spacing1;
    }
  }
}


.section{
  padding-top: 100px;
  padding-bottom: 100px;
}

.section-header{
  margin: 0px auto 80px auto;
  width: 60vw; 
  text-align: center;

  @include media-query($tablet){
    width: 80vw;
  }

  @include media-query($mobile){
    .subtitle{
      display: none;
    }
  }

}

.section-features{
  @include bg-schema($primary, $light);
  @include padding-lateral;

  .cards-container{
    display: grid;
    grid-template-columns: repeat(auto-fit, 312px);   
    justify-content: space-around;
    row-gap: $spacing5;
  }
}

.contato-backdrop{
  z-index: 19;  
  &.closed{
    display: none;
  }
}

.section-contato{
  @include bg-schema($primary-light, $dark);
  @include size(540px, 480px);
  
  top: calc(50vh - 240px);
  left: calc(50% - 270px);
  position: fixed;
  border-radius: $rounded1;
  z-index: 20;
  padding: 25px 50px;  

  @include media-query($mobile){
    width: 310px;
    left: calc(50% - 155px);
    padding: 25px 20px;
  }
  
  &.closed{
    display: none;
  }

  .close-btn{
    position: absolute;
    top: $spacing3;
    right: $spacing3;
    cursor: pointer;
  }

  h2{
    text-align: center;
    margin-bottom: 10px;
  }

  
  input, textarea{
    @include form-input;
    border: 1px solid #999;
    width: 100%;  
    margin-bottom: 10px; 
  }

  textarea{
    height: 100px;
    resize: none;
  }

  input[type=submit]{
    @include bg-schema($dark, $light);
    height: 40px;
    border: none;
    font-weight: 500;
    cursor: pointer;
  }

  label{
    font-weight: 500;
    line-height: 30px;
  }

}